import React from 'react'
import { Badge } from 'react-bootstrap'
import { useSelector } from 'react-redux'

function Counter() {
  const state = useSelector(state => state)
  const allTasks = state.length
  const doneTasks = state.filter(e => e.statu === 'done').length

  return (
    <div>
      <label>All Tasks:</label>
      <Badge style={{ fontSize: 12 }} className='p-1 m-2' pill bg={allTasks === 0 ? 'warning' : 'primary'} text={allTasks !== 0 && 'light'}>
        {allTasks ? allTasks : 'Zero'}
      </Badge>{' '}
      <label>Done Tasks:</label>
      <Badge style={{ fontSize: 12 }} className='p-1 m-2' pill bg={doneTasks === 0 ? 'warning' : 'success'} text={doneTasks !== 0 && 'light'}>
        {doneTasks ? doneTasks : 'Zero'}
      </Badge>{' '}
    </div>
  )
}

export default Counter
