import * as actions from './actionTypes'

export const AddTask = text => ({
  type: actions.ADD_TASK,
  payload: { text },
})

export const RemoveTask = id => {
  return {
    type: actions.REMOVE_TASK,
    payload: { id },
  }
}

export const DoneTask = id => ({
  type: actions.DONE_TASK,
  payload: { id },
})

export const UnDoneTask = id => ({
  type: actions.UN_DONE_TASK,
  payload: { id },
})

export const ReStore = state => ({
  type: actions.RESTORE_TASKS,
  payload: state,
})

export const MoveUp = (id, index) => ({
  type: actions.TASK_UP,
  payload: { id, index },
})

export const MoveDown = (id, index) => ({
  type: actions.TASK_DOWN,
  payload: { id, index },
})
